@media only screen and (max-width: 1023px) {
	.key {
		flex: 1;
		font-size: 1.5rem;
		min-width: 1.5rem;
	}

	.key.wide {
		/* font-size: 4rem; */
		width: 4rem;
	}
}

@media only screen and (min-width: 1024px) {
	.key {
		font-size: 3rem;
		min-width: 3rem;
	}

	.key.wide {
		/* font-size: 3rem; */
		width: 6rem;
	}
}

.key {
	/* min-width: 3rem; */
	/* padding: 5px; */
	/* width: 3rem; */
	background-color: #787c7e;
	font-weight: bold;
	/* font-size: 3rem; */
	color: white;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	margin: 4px;
	cursor: pointer;
}

.key.green {
	background-color: #6aaa64;
}

.key.yellow {
	background-color: #c9b458;
}

.key.absent {
	background-color: #3d4848;
}